.line-through {
  text-decoration: line-through;
  user-select: none; }

.search .searchForm input[type=search] {
  -moz-appearance: none;
  appearance: none;
  -webkit-appearance: textfield;
  min-width: 75%;
  height: 30px;
  line-height: 30px;
  padding: 5px;
  border: 1px solid #FAFAFA;
  box-shadow: inset 0 5p 5px #7b7b7b;
  margin-bottom: 50px; }

.search .searchName > p {
  font-size: 1.3em;
  font-family: Roboto Slab, serif;
  margin-bottom: 1.92308em;
  color: #FF0000;
  font-weight: 300;
  line-height: 1.5em;
  margin-top: 20px; }

.search h4 {
  font-size: 1.6em;
  font-family: Roboto Slab, serif;
  margin-bottom: 0.78125em;
  color: #FF0000;
  font-weight: 300;
  line-height: 1.1em; }
