.line-through {
  text-decoration: line-through;
  user-select: none; }

.Pagination {
  background: transparent;
  display: grid;
  grid-template-columns: 10px 100px 10px;
  grid-gap: 10px; }

:global .PaginationContainer {
  width: 100%;
  height: 16px;
  align-items: center;
  color: #636363;
  font-size: 14px;
  margin: 0 0 50px 0; }

:global .PaginationLeft {
  cursor: pointer;
  transform: rotate(180deg);
  min-height: 16px;
  min-width: 8px;
  align-self: center; }

:global .PaginationRight {
  cursor: pointer;
  min-height: 16px;
  min-width: 8px;
  align-self: center; }

.PaginationText {
  text-align: center;
  justify-self: self-start;
  align-self: flex-start; }

.Active {
  background: transparent; }

.InActive {
  opacity: .4; }

